<template>
  <div id="withDrawal">
    <div class="content">
      <van-form class="formBox" ref="formRef">
        <!-- 提现金额 -->
        <van-cell-group class="formCell moneyBox">
          <van-field
            placeholder="请输入您的提现金额"
            v-model="formData.money"
            :rules="formRule.money"
            :error="false"
            name="money"
          >
            <template #left-icon>
              <p class="leftIcon">￥</p>
            </template>
            <template #button>
              <van-button
                round
                block
                type="info"
                native-type="button"
                class="withDrawalAll"
                @click="withDrawalAll"
              >全部提现</van-button>
            </template>
          </van-field>
        </van-cell-group>
        <!--  -->
      </van-form>
      <p class="balance">可提现金额 ￥{{formData.can_withdraw}}</p>
      <div class="withDrawalRule"></div>
      <div class="submitBox">
        <van-button type="default" block class="withDrawalSubmit" @click="withDrawalSubmit">确认提现</van-button>
      </div>
    </div>
    <van-overlay :show="load" :z-index="10000" class="loadBox" :lock-scroll="true">
      <van-loading class="load" type="spinner" color="#1989fa" :vertical="true" v-if="load" />
    </van-overlay>
  </div>
</template>
<script>
import {
  getWithDrawalData,
  withDrawalSubmit,
} from "../assets/javascript/apis.js";
export default {
  data() {
    var checkMoney = (value, rule) => {
      var num = (function (value) {
        if (value) {
          value = parseFloat(value);
          value = value.toLocaleString();
          let xsd = value.split(".");
          if (xsd.length == 1) {
            return value + ".00";
          }
          if (xsd.length > 1) {
            if (xsd[1].length == 1) {
              return value + "0";
            } else {
              return value;
            }
          }
        } else {
          return "0.00";
        }
      })(value);
      if (num !== "0.00") {
        return true;
      }
      return false;
    };
    return {
      load: true,
      withDrawalFlag: true,
      formData: {
        money: "",
        can_withdraw: 0,
      },
      formRule: {
        money: [
          {
            required: true,
            message: "提现金额不可为空",
            trigger: "onChange",
          },
          {
            pattern: /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/,
            message: "只能输入正整数，或者小数位不超过两位的数字",
            trigger: "onChange",
          },
          {
            validator: checkMoney,
            trigger: "onChange",
            message: "提现金额不能为0",
          },
        ],
      },
    };
  },
  created() {
    this.getData();
  },
  methods: {
    //获取页面初始可提现余额数据
    getData() {
      this.load = true;
      getWithDrawalData().then((res) => {
        // this.formData.can_withdraw = 1314;
        this.formData.can_withdraw = res.can_withdraw;
        this.load = false;
      });
    },
    //点击提现申请
    withDrawalSubmit() {
      //首先进行表单验证
      this.$refs.formRef
        .validate()
        .then((res) => {
          let money = Number(this.formData.money),
            can_withdraw = Number(this.formData.can_withdraw);
          //判断提现金额与可提现金额
          if (money < can_withdraw) {
            //判断上次提现申请接口是否回复
            if (!this.withDrawalFlag) return;
            this.withDrawalFlag = false;

            const data = {
              amount: this.formData.money,
            };
            //发起申请
            withDrawalSubmit(data).then((res) => {
              // console.log(res);
              this.withDrawalFlag = true;
              //进行提现申请的状态判断
              if (res.status == "success") {
                //提现成功
                this.$dialog.alert({
                  message: "提现申请成功!",
                });
              } else {
                //提现失败
                this.$dialog.alert({
                  message: "提现申请失败，请重新申请!",
                });
              }
            });
          } else {
            this.$toast({
              position: "top",
              message: "输入提现金额不可大于可提现金额",
            });
          }
        })
        .catch((res) => {
          console.log(res);
        });
    },
    //点击全部提现
    withDrawalAll() {
      this.formData.money = this.formData.can_withdraw;
    },
  },
};
</script>
<style lang="scss" scoped>
#withDrawal {
  width: 100%;
  padding: 0 15px;
  box-sizing: border-box;
  .loadBox {
    background: white;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .content {
    width: 100%;
    /deep/.formBox {
      width: 100%;
      margin-top: 35px;
      .formCell {
        .van-field {
          width: 100%;
          height: 45px;
          padding: 0;
          border-bottom: 1px solid rgba(238, 238, 238, 1);
          overflow: inherit;
          .van-field__value {
            position: relative;
            .van-field__body {
              input {
                width: 100%;
                height: 45px;
                padding: 0 30px;
                font-size: 16px;
              }
            }
            .van-field__error-message {
              position: absolute;
              top: 100%;
              left: 30px;
              z-index: 10000;
            }
          }
          .van-field__left-icon {
            .leftIcon {
              line-height: 45px;
              font-size: 24px;
              color: rgba(7, 7, 7, 1);
            }
          }
        }
        &.moneyBox {
          .withDrawalAll {
            width: 85px;
            height: 25px;
            padding: 0;
            background: linear-gradient(
              0deg,
              rgba(76, 170, 241, 1),
              rgba(155, 120, 240, 1)
            );
            border-radius: 12px;
            font-size: 14px;
            color: rgba(255, 255, 255, 1);
          }
        }
      }
    }
    .balance {
      margin-top: 30px;
      font-size: 12px;
      color: rgba(170, 170, 170, 1);
    }
    .withDrawalRule {
      width: 100%;
      height: 500px;
      margin: 40px 0 110px 0;
      background: rgba(238, 238, 238, 1);
    }
    .submitBox {
      width: 100%;
      padding: 0 15px;
      box-sizing: border-box;
      position: fixed;
      left: 0;
      bottom: 0;
      background: white;
      .withDrawalSubmit {
        height: 40px;
        margin: 10px 0;
        background: rgba(76, 170, 241, 1);
        border-radius: 10px 10px 10px 10px;
        font-size: 15px;
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
      }
    }
  }
}
</style>